<template>
  <ValidationObserver ref="observer">
    <form autocomplete="false" @submit.prevent="saveNewDestinatary">
      <h2 style="margin: 25px 0;">Nuevo destinatario</h2>
      <v-container style="padding: 12px 10rem;">
          <v-row justify="space-between">
            <ValidationProvider
                  v-slot="{ errors }"
                  name="name"
                  rules="required"
                >
              <v-text-field
                id="name"
                v-model="destinatary.name"
                label="Nombre"
                rounded
                outlined
                dense
                class="mx-auto"
                style="width: 400px !important"
                :error-messages="errors"
                :hide-details="errors ? false : true"
              ></v-text-field>
            </ValidationProvider>
            <ValidationProvider
                  v-slot="{ errors }"
                  name="lastname"
                  rules="required"
                >
              <v-text-field
                id="lastname"
                v-model="destinatary.lastname"
                label="Apellidos"
                rounded
                outlined
                dense
                class="mx-auto"
                style="width: 400px !important"
                :error-messages="errors"
                :hide-details="errors ? false : true"
              ></v-text-field>
            </ValidationProvider>
          </v-row>
          <v-row justify="space-between">
            <ValidationProvider
                  v-slot="{ errors }"
                  name="phone"
                >
              <v-text-field
                id="phone"
                v-model="destinatary.phone"
                label="Telfono"
                rounded
                outlined
                dense
                class="mx-auto"
                style="width: 400px !important"
                :error-messages="errors"
                :hide-details="errors ? false : true"
              ></v-text-field>
            </ValidationProvider>
            <ValidationProvider
                  v-slot="{ errors }"
                  name="id"
                  rules="required"
                >
              <v-text-field
                id="id"
                v-model="destinatary.id"
                label="ID"
                rounded
                outlined
                dense
                class="mx-auto"
                style="width: 400px !important"
                :error-messages="errors"
                :hide-details="errors ? false : true"
              ></v-text-field>
            </ValidationProvider>
          </v-row>
          <v-row justify="space-between">
            <ValidationProvider
                  v-slot="{ errors }"
                  name="address1"
                  rules="required"
                >
              <v-text-field
                id="address1"
                v-model="destinatary.address1"
                label="Direccin (Linea 1)"
                rounded
                outlined
                dense
                class="mx-auto"
                style="width: 400px !important"
                :error-messages="errors"
                :hide-details="errors ? false : true"
              ></v-text-field>
            </ValidationProvider>
            <ValidationProvider
                  v-slot="{ errors }"
                  name="address2"
                >
              <v-text-field
                id="address2"
                v-model="destinatary.address2"
                label="Direccin (Linea 2)"
                rounded
                outlined
                dense
                class="mx-auto"
                style="width: 400px !important"
                :error-messages="errors"
                :hide-details="errors ? false : true"
              ></v-text-field>
            </ValidationProvider>
          </v-row>
          <v-row justify="space-between">
            <ValidationProvider
                  v-slot="{ errors }"
                  name="city"
                  rules="required"
                >
              <v-text-field
                id="city"
                v-model="destinatary.city"
                label="Cuidad"
                rounded
                outlined
                dense
                class="mx-auto"
                style="width: 400px !important"
                :error-messages="errors"
                :hide-details="errors ? false : true"
              ></v-text-field>
            </ValidationProvider>
            <ValidationProvider
                  v-slot="{ errors }"
                  name="state"
                  rules="required"
                >
              <v-text-field
                id="state"
                v-model="destinatary.state"
                label="Estado"
                rounded
                outlined
                dense
                class="mx-auto"
                style="width: 400px !important"
                :error-messages="errors"
                :hide-details="errors ? false : true"
              ></v-text-field>
            </ValidationProvider>
          </v-row>
          <v-row justify="space-between">
                <ValidationProvider
                  v-slot="{ errors }"
                  name="alternative_destinatary"
                >
              <v-text-field
                id="alternative_destinatary"
                v-model="destinatary.alternative_destinatary"
                label="Nombre del destinatario alternativo"
                rounded
                outlined
                dense
                class="mx-auto"
                style="width: 400px !important"
                :error-messages="errors"
                :hide-details="errors ? false : true"
                @input="destinatary.deny_alternative_destinatary = false"
              ></v-text-field>
            </ValidationProvider>
            <v-menu
                  ref="date_menu"
                  v-model="date_menu"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="delivery_date"
                      rules="required"
                    >
                      <v-text-field
                        id="delivery_date"
                        v-model="destinatary.delivery_date"
                        label="Fecha y hora del envo"
                        color="primary"
                        persistent-hint
                        append-inner-icon="mdi-calendar"
                        v-bind="attrs"
                        @blur="date = parseDate(destinatary.delivery_date)"
                        v-on="on"
                        class="mx-auto"
                        style="width: 400px !important"
                        :error-messages="errors"
                        :hide-details="errors ? false : true"
                        clearable
                        dense
                        outlined
                        rounded
                      ></v-text-field>
                    </ValidationProvider>
                  </template>
                  <v-date-picker
                    v-model="date"
                    no-title
                    color="primary"
                    @input="date_menu = false"
                  ></v-date-picker>
                </v-menu>
          </v-row>
          <v-row justify="space-between">
            <v-checkbox
              v-model="destinatary.deny_alternative_destinatary"
              label="Yo confirmo no dar destinatario alternativo"
              :disabled="destinatary.alternative_destinatary != null && destinatary.alternative_destinatary != ''"
            ></v-checkbox>
          </v-row>
          <v-row justify="center">
            <v-btn type="button" class="mx-2" outlined rounded elevation="0" color="error" @click="cancel">Cancelar</v-btn>
            <v-btn type="submit" class="mx-2" outlined rounded elevation="0" color="primary">Guardar</v-btn>
          </v-row>
      </v-container>
    </form>
  </ValidationObserver>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'

extend('required', {
  validate (value) {
    return {
      required: true,
      valid: ['', null, undefined].indexOf(value) === -1
    }
  },
  message: 'Rellene este campo.',
  computesRequired: true
})

extend('afterToday', {
  validate (value) {
    const [day, month, year] = value.split('/')
    const date = new Date(`${month}/${day}/${year}`)
    const today = new Date()
    return date.getTime() >= today.getTime()
  },
  message: 'Escoja una fecha posterior a la actual.'
})

extend('moreThanZero', {
  validate (value) {
    return value >= 1
  },
  message: 'Escoja un valor vlido.'
})

extend('positive', {
  validate (value) {
    return value >= 0
  },
  message: 'Escoja un valor vlido.'
})

export default {
  name: 'NewClient',
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data: function () {
    return {
      date: null,
      date_menu: null,
      destinatary: {
        // name: null,
        // lastname: null,
        // phone: null,
        // id: null,
        // address1: null,
        // address2: null,
        // city: null,
        // state: null,
        // alternative_destinatary: null,
        // delivery_date: null,
        // deny_alternative_destinatary: false
        name: 'Alfredo Suarez',
        lastname: 'Garrido',
        phone: '+1 568 5546 568',
        id: '89565123165',
        address1: 'Cuba',
        address2: null,
        city: 'La habana',
        state: 'Playa',
        alternative_destinatary: 'Yazmin Reyes',
        delivery_date: null,
        deny_alternative_destinatary: false
      }
    }
  },
  watch: {
    date (val) {
      this.destinatary.delivery_date = this.formatDate(this.date)
    }
  },
  methods: {
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    saveNewDestinatary () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          const alternative = !this.destinatary.deny_alternative_destinatary ? '   (Destinatario alternativo: ' + this.destinatary.alternative_destinatary + ')' : ''
          const newDestinatary = this.destinatary.name + ' ' + this.destinatary.lastname + alternative
          console.log('alternative', alternative)
          console.log('newDestinatary', newDestinatary)
          this.$emit('newDestinatarySaved', newDestinatary)
        }
      })
    },
    cancel () {
      this.$emit('cancel')
    }
  }
}
</script>

<style lang="scss" scoped>
.row{
  margin: 7px 0 !important;
}
</style>
