<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="desserts"
      item-key="name"
      class="elevation-1"
      :search="search"
    >
      <template v-slot:top>
        <v-row align="center">
          <h2>Ordenes realizadas</h2>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Buscar orden"
            class="mx-4"
            single-line
            hide-details
          ></v-text-field>
        </v-row>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-btn
          text
          color="light-blue darken-4"
          @click="showOder(item)"
        >
          Ver orden
        </v-btn>
      </template>
      <template v-slot:no-data>
        <v-btn
          color="primary"
          @click="initialize"
        >
          Reset
        </v-btn>
      </template>
    </v-data-table>

    <v-dialog v-model="showOderInformation" max-width="500px">
      <v-card>
        <v-card-title class="text-center">Aqui se mostrar la informacion de la orden</v-card-title>
        <v-card-actions class="d-flex flex-row align-center justify-end">
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="showOderInformation = false">Cerrar</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: 'OrderHistory',
  data () {
    return {
      search: '',
      calories: '',
      showOderInformation: false,
      desserts: [
        {
          name: 'Frozen Yogurt',
          calories: 159,
          fat: 6.0,
          carbs: 24,
          protein: 4.0,
          iron: '1%'
        },
        {
          name: 'Ice cream sandwich',
          calories: 237,
          fat: 9.0,
          carbs: 37,
          protein: 4.3,
          iron: '1%'
        },
        {
          name: 'Eclair',
          calories: 262,
          fat: 16.0,
          carbs: 23,
          protein: 6.0,
          iron: '7%'
        },
        {
          name: 'Cupcake',
          calories: 305,
          fat: 3.7,
          carbs: 67,
          protein: 4.3,
          iron: '8%'
        }
      ]
    }
  },
  computed: {
    headers () {
      return [
        {
          text: 'Restaurante',
          align: 'start',
          sortable: false,
          value: 'name'
        },
        {
          text: '# de orden',
          align: 'center',
          value: 'calories'
          // filter: value => {
          //   if (!this.calories) return true

          //   return value < parseInt(this.calories)
          // }
        },
        { text: 'Fecha', align: 'center', value: 'fat' },
        { text: 'Importe Final', align: 'center', value: 'carbs' },
        { text: 'Enviado a', align: 'center', value: 'protein' },
        { text: 'Estado', align: 'center', value: 'iron' },
        { text: 'Acciones', align: 'center', value: 'actions', sortable: false }
      ]
    }
  },
  methods: {
    filterOnlyCapsText (value, search, item) {
      return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleUpperCase().indexOf(search) !== -1
    },
    showOder (item) {
      // this.editedIndex = this.desserts.indexOf(item)
      // this.editedItem = Object.assign({}, item)
      this.showOderInformation = true
    }
  }
}
</script>

<style lang="scss" scoped>
.v-btn{
  font-size: 11px;
}
.row{
  margin: 18px 12px;
}
</style>
