<template>
  <div>
        <section v-if="slideMode" id="products-slider-container">
          <v-row align="center">
            <v-col id="restaurant-info" cols="auto">
              <p>Nombre del restaurante</p>
              <p>Tipo de concina</p>
              <p>Tipo de entrega</p>
            </v-col>
          </v-row>
          <div class="slider-container">
            <v-row align-self="center">
              <v-col align="center" style="width: 79.5% !important;">
                <v-slide-group
                  id="products-slider"
                  class="pa-2 d-flex flex-row align-center justify-center"
                  center-active
                  active-class="success"
                  show-arrows="always"
                >
                    <v-slide-item v-for="product in products" :key="product.name" v-slot="{ toggle }">
                      <Product :product="product" @click="toggle"/>
                    </v-slide-item>

                </v-slide-group>
              </v-col>
              <v-col align-self="center">
                <v-btn elevation="0" to="/restaurant/cubamax/1/details">VER TODOS</v-btn>
              </v-col>
            </v-row>
          </div>
        </section>

        <section id="products-container">
          <v-row v-if="!slideMode && !listSize" class="my-7 justify-center" dense>
            <v-col cols="8">
              <Product v-for="(product, i) in products" :key="i" :product="product" :horizontal="true"/>
            </v-col>
          </v-row>
          <div v-if="!slideMode && !listSize" class="text-center">
              <v-pagination circle
              v-model="pagination.page"
              :length="Math.ceil(this.products.length / 12)"
              :total-visible="15"
              @input="paginate"
              @next="paginate"
              @previous="paginate"
              ></v-pagination>
          </div>
          <v-row v-if="!slideMode && listSize">
            <v-col id="all-products-button" class="d-flex flex-column align-center">
              <v-btn elevation="0" style="margin-left: 0 !important; margin: 20px 0px 12px 0px !important;">Cargar ms productos</v-btn>
            </v-col>
          </v-row>
        </section>
  </div>
</template>

<script>
import store from '../store'
import Product from '@/components/Product'

export default {
  name: 'ProductsList',
  props: {
    slideMode: {
      type: Boolean,
      default: false
    },
    listSize: {
      type: Number,
      required: false,
      default: 0
    }
  },
  components: {
    Product
  },
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      products: this.$store.getters.products(),
      pageProducts: []
    }
  },
  created: function () {
    if (this.slideMode && this.listSize > 0) {
      this.products = this.products.slice(0, this.listSize)
    }
    this.pageProducts = this.products.slice(this.pagination.offset, this.pagination.limit * this.pagination.page)
  },
  computed: {
    amoutn: {
      get () {
        return store.getters.checkProductOnCart(this.product.id)
      }
    }
  },
  methods: {
    addToCart (product) {
      this.$store.dispatch('addToCart', product)
    },
    checkOnFavorites (product) {
      if (product.isFavorite) {
        this.$store.dispatch('addToFavorites', product)
      } else {
        this.$store.dispatch('removeFromFavorites', product)
      }
      product.isFavorite = !product.isFavorite
    },
    checkOnCart (product) {
      return store.getters.checkProductOnCart(product.id)
    },
    getImgUrl (image) {
      var images = require.context('../assets', false)
      return images('./' + image)
    },
    seeDetails (product) {
      this.$store.dispatch('updateProductDetails', product)
    },
    paginate () {
      this.pagination.offset = this.pagination.limit * this.pagination.page - this.pagination.limit
      this.pageProducts = this.products.slice(this.pagination.offset, this.pagination.limit * this.pagination.page)
    }
  }
}
</script>

