const mutations = {

  addToCart (state, product) {
    // const i = state.cart.findIndex(stored => stored.id === product.id)
    // if (i !== -1) {
    //   state.cart[i].count = state.cart[i].count + 1
    //   console.log('updated', state.cart[i].count)
    // } else {
    //   console.log('added')
    //   product.count = 1
    //   state.cart.push(product)
    // }
    state.cart.push(product)
  },

  removeFromCart (state, product) {
    state.cart = state.cart.filter(item => item.id !== product.id)
  },

  clearCart (state) {
    state.cart = []
  },

  addToFavoriteProducts (state, product) {
    if (state.favoriteProducts.filter(item => item.id === product.id).length === 0) {
      state.favoriteProducts.push(product)
    }
  },

  removeFromFavoriteProducts (state, product) {
    state.favoriteProducts = state.favoriteProducts.filter(item => item.id !== product.id)
  }
}

const actions = {

  updateProductDetails ({ commit }, product) {
    commit('updateProductDetails', product)
    router.replace('/product-details')
  },

  addToCart ({ commit }, product) {
    commit('addToCart', product)
  },

  removeFromCart ({ commit }, product) {
    commit('removeFromCart', product)
  },

  clearCart ({ commit }) {
    commit('clearCart')
  },

  addToFavoriteProducts ({ commit }, product) {
    commit('addToFavoriteProducts', product)
  },

  removeFromFavoriteProducts ({ commit }, product) {
    commit('removeFromFavoriteProducts', product)
  }

}

const getters = {
  cart: (state) => () => {
    return state.cart
  },
  favoriteProducts: (state) => () => {
    return state.favoriteProducts
  },
  checkProductOnCart: (state) => (id) => {
    // const i = state.cart.findIndex(stored => stored.id === id)
    // if (i !== -1) {
    //   console.log(state.cart[i].count)
    //   return state.cart[i].count
    // } else {
    //   return 0
    // }
    return state.cart.filter(product => product.id === id).length
  },
  checkProductOnFavorites: (state) => (id) => {
    return state.favoriteProducts.filter(product => product.id === id).length
  },
  products: (state) => () => {
    return state.products
  },
  getProduct: (state) => (index) => {
    return state.products[index]
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
