const mutations = {

  addToFavorites (state, blog) {
    if (state.favorites.filter(item => item.id === blog.id).length === 0) {
      state.favorites.push(blog)
    }
  },
  removeFromFavorites (state, blog) {
    state.favorites = state.favorites.filter(item => item.id !== blog.id)
  }
}

const actions = {

  updateBlogDetails ({ commit }, blog) {
    commit('updateBlogDetails', blog)
    router.replace('/blog-details')
  },

  addToFavorites ({ commit }, blog) {
    commit('addToFavorites', blog)
  },

  removeFromFavorites ({ commit }, blog) {
    commit('removeFromFavorites', blog)
  }

}

const getters = {
  favoriteBlogs: state => {
    return state.favorites
  },
  blogs: (state) => () => {
    return state.blogs
  },
  getBlog: (state) => (index) => {
    return state.blogs[index]
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
