<template>
  <div>
    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="desserts"
      item-key="name"
      show-select
      class="elevation-1"
      :search="search"
    >
      <!-- Top row -->
      <template v-slot:top>
        <v-row align="center">
          <h2>LISTA DE CONTACTOS</h2>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Buscar contacto"
            class="mx-4"
            single-line
            hide-details
          ></v-text-field>
        </v-row>

        <!-- Modal for edit action -->
        <v-row align="center" justify="end">
          <v-dialog
            v-model="dialog"
            max-width="500px"
            v-if="selected.length == 0"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
              >
                Agregar contacto
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        v-model="editedItem.name"
                        label="Nombre"
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        v-model="editedItem.phone"
                        label="Telfono"
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                    >
                      <v-textarea
                        v-model="editedItem.address"
                        label="Direccin"
                        rows="2"
                        auto-grow
                        hide-details
                      ></v-textarea>
                    </v-col>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        v-model="editedItem.id"
                        label="ID"
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        v-model="editedItem.card"
                        label="Tarjeta AIS"
                        hide-details
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="save"
                >
                  Guardar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog
            v-model="dialog"
            max-width="500px"
            v-if="selected.length > 0"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="error"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
              >
                Eliminar seleccionados
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">Seguro que desea eliminar los contactos seleccionados?</span>
              </v-card-title>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="deleteSelectedContactos"
                >
                  Eliminar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
      </template>

      <!-- Actions column -->
      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>

      <template v-slot:no-data>
        <v-btn
          color="primary"
          @click="initialize"
        >
          Reset
        </v-btn>
      </template>
    </v-data-table>

    <!-- Modal for delete action -->
    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
          <v-btn color="blue darken-1" text @click="deleteItemConfirm">Eliminar</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: 'Destinataries',
  data () {
    return {
      search: '',
      selected: [],
      address: '',
      dialog: false,
      dialogDelete: false,
      editedIndex: -1,
      editedItem: {
        name: '',
        address: '',
        id: '',
        phone: '',
        card: ''
      },
      defaultItem: {
        name: '',
        address: '',
        id: '',
        phone: '',
        card: ''
      },
      desserts: [
        {
          name: 'Frozen Yogurt',
          address: '159',
          id: 6.0,
          phone: 24,
          card: 4.0,
          iron: '1%'
        },
        {
          name: 'Ice cream sandwich',
          address: '237',
          id: 9.0,
          phone: 37,
          card: 4.3,
          iron: '1%'
        },
        {
          name: 'Eclair',
          address: '262',
          id: 16.0,
          phone: 23,
          card: 6.0,
          iron: '7%'
        },
        {
          name: 'Cupcake',
          address: '305',
          id: 3.7,
          phone: 67,
          card: 4.3,
          iron: '8%'
        }
      ]
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    }
  },
  created () {
    this.initialize()
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nuevo contacto' : 'Editar contacto'
    },
    headers () {
      return [
        {
          text: 'Nombre',
          align: 'start',
          sortable: false,
          value: 'name'
        },
        {
          text: 'Telfono',
          align: 'start',
          sortable: false,
          value: 'phone'
        },
        {
          text: 'Direccin',
          align: 'start',
          value: 'address'
          // filter: value => {
          //   if (!this.address) return true

          //   return value < parseInt(this.address)
          // }
        },
        { text: 'ID', align: 'start', value: 'id' },
        { text: 'Tarjeta AIS', align: 'start', value: 'card' },
        { text: 'Acciones', align: 'center', value: 'actions', sortable: false }
      ]
    }
  },
  methods: {
    filterOnlyCapsText (value, search, item) {
      return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleUpperCase().indexOf(search) !== -1
    },
    editItem (item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.desserts.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem)
      } else {
        this.desserts.push(this.editedItem)
      }
    },

    deleteSelectedContactos () {
      // if (this.editedIndex > -1) {
      //   Object.assign(this.desserts[this.editedIndex], this.editedItem)
      // } else {
      //   this.desserts.push(this.editedItem)
      // }
      this.close()
    }
  }
}
</script>

<style lang="scss" scoped>
.v-btn{
  font-size: 11px;
}
.row{
  margin: 18px 12px;
}
.theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr:last-child > th:first-child {
  width: 50px !important;
  text-align: center;
}
</style>
