<template>
  <v-main>
    <v-container>
      <h1>Informacion de pago</h1>
      <v-row>
        <v-col cols="3" class="pa-4 my-9" align="center" style="border: 1px solid black">
          LOGOS TARJETAS
        </v-col>
      </v-row>
      <ValidationObserver ref="observer">
        <form autocomplete="false" @submit.prevent="payOrder">
          <v-container style="padding: 12px 10rem;">
              <v-row justify="space-between">
                <ValidationProvider
                      v-slot="{ errors }"
                      name="card"
                      rules="required"
                    >
                  <v-text-field
                    id="card"
                    v-model="pay.card"
                    label="Nmero de tarjeta"
                    rounded
                    outlined
                    dense
                    class="mx-auto"
                    style="width: 400px !important"
                    :error-messages="errors"
                    :hide-details="errors ? false : true"
                  ></v-text-field>
                </ValidationProvider>
                <ValidationProvider
                      v-slot="{ errors }"
                      name="digits"
                      rules="required"
                    >
                  <v-text-field
                    id="digits"
                    v-model="pay.digits"
                    label="Dgitos de seguridad (CVV2/CVC2)"
                    rounded
                    outlined
                    dense
                    class="mx-auto"
                    style="width: 400px !important"
                    :error-messages="errors"
                    :hide-details="errors ? false : true"
                  ></v-text-field>
                </ValidationProvider>
              </v-row>
              <v-row justify="space-between">
                <ValidationProvider
                      v-slot="{ errors }"
                      name="expiration_month"
                      rules="required"
                    >
                  <v-text-field
                    id="expiration_month"
                    v-model="pay.expiration_month"
                    label="Mes de vencimiento"
                    rounded
