                    dense
                    class="mx-auto"
                    style="width: 400px !important"
                    :error-messages="errors"
                    :hide-details="errors ? false : true"
                  ></v-text-field>
                </ValidationProvider>
                <ValidationProvider
                      v-slot="{ errors }"
                      name="expiration_year"
                      rules="required"
                    >
                  <v-text-field
                    id="expiration_year"
                    v-model="pay.expiration_year"
                    label="Ao de vencimiento"
                    rounded
                    outlined
                    dense
                    class="mx-auto"
                    style="width: 400px !important"
                    :error-messages="errors"
                    :hide-details="errors ? false : true"
                  ></v-text-field>
                </ValidationProvider>
              </v-row>
              <v-row justify="space-between">
                <ValidationProvider
                      v-slot="{ errors }"
                      name="name"
                      rules="required"
                    >
                  <v-text-field
                    id="name"
                    v-model="pay.name"
                    label="Nombre"
                    rounded
                    outlined
                    dense
                    class="mx-auto"
                    style="width: 400px !important"
                    :error-messages="errors"
                    :hide-details="errors ? false : true"
                  ></v-text-field>
                </ValidationProvider>
                <ValidationProvider
                      v-slot="{ errors }"
                      name="lastname"
                      rules="required"
                    >
                  <v-text-field
                    id="lastname"
                    v-model="pay.lastname"
                    label="Apellidos"
                    rounded
                    outlined
                    dense
                    class="mx-auto"
                    style="width: 400px !important"
                    :error-messages="errors"
                    :hide-details="errors ? false : true"
                  ></v-text-field>
                </ValidationProvider>
              </v-row>
              <v-row justify="space-between">
                <ValidationProvider
                      v-slot="{ errors }"
                      name="address1"
                      rules="required"
                    >
                  <v-text-field
                    id="address1"
                    v-model="pay.address1"
                    label="Direccin (Linea 1)"
                    rounded
                    outlined
                    dense
                    class="mx-auto"
                    style="width: 400px !important"
                    :error-messages="errors"
                    :hide-details="errors ? false : true"
                  ></v-text-field>
                </ValidationProvider>
                <ValidationProvider
