                    style="width: 400px !important"
                    :error-messages="errors"
                    :hide-details="errors ? false : true"
                  ></v-text-field>
                </ValidationProvider>
              </v-row>
              <v-row justify="space-between">
                <ValidationProvider
                      v-slot="{ errors }"
                      name="city"
                      rules="required"
                    >
                  <v-text-field
                    id="city"
                    v-model="pay.city"
                    label="Cuidad"
                    rounded
                    outlined
                    dense
                    class="mx-auto"
                    style="width: 400px !important"
                    :error-messages="errors"
                    :hide-details="errors ? false : true"
                  ></v-text-field>
                </ValidationProvider>
                <ValidationProvider
                      v-slot="{ errors }"
                      name="state"
                      rules="required"
                    >
                  <v-text-field
                    id="state"
                    v-model="pay.state"
                    label="Estado"
                    rounded
                    outlined
                    dense
                    class="mx-auto"
                    style="width: 400px !important"
                    :error-messages="errors"
