                    >
                  <v-text-field
                    id="postal_code"
                    v-model="pay.postal_code"
                    label="Postal code"
                    rounded
                    outlined
                    dense
                    class="mx-auto"
                    style="width: 400px !important"
                    :error-messages="errors"
                    :hide-details="errors ? false : true"
                  ></v-text-field>
                </ValidationProvider>
                <h1>Total a pagar: $1.60</h1>
              </v-row>
              <v-row justify="center" class="py-14">
                <!-- <v-btn type="button" class="mx-2" outlined rounded elevation="0" color="error" @click="cancel">Cancelar</v-btn> -->
                <v-btn type="submit" class="mx-2" rounded elevation="0" color="primary">Pagar</v-btn>
              </v-row>
          </v-container>
        </form>
      </ValidationObserver>
    </v-container>
  </v-main>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'

extend('required', {
  validate (value) {
    return {
      required: true,
      valid: ['', null, undefined].indexOf(value) === -1
    }
  },
  message: 'Rellene este campo.',
  computesRequired: true
})

extend('afterToday', {
  validate (value) {
    const [day, month, year] = value.split('/')
    const date = new Date(`${month}/${day}/${year}`)
    const today = new Date()
    return date.getTime() >= today.getTime()
  },
  message: 'Escoja una fecha posterior a la actual.'
})

extend('moreThanZero', {
  validate (value) {
    return value >= 1
  },
  message: 'Escoja un valor vlido.'
})

extend('positive', {
  validate (value) {
    return value >= 0
  },
  message: 'Escoja un valor vlido.'
})

export default {
  name: 'PayOrder',
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data: function () {
    return {
      pay: {
        card: null,
        digits: null,
        expiration_month: null,
        expiration_year: null,
        name: 'Alfredo Suarez',
        lastname: 'Garrido',
        address1: 'Cuba',
        address2: null,
        city: 'La habana',
        state: 'Playa',
        post_code: '58465468'
      }
    }
  },
  methods: {
    payOrder () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          alert('Paid out!')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.container{
  margin: 3rem auto;
}
.row{
  margin: 7px 0 !important;
}
</style>
