<template>
  <div>
    <div v-if="!horizontal">
      <v-hover>
        <v-scale-transition></v-scale-transition>
        <template v-slot:default="{ hover }">
          <v-card
            class="ma-2 rounded-lg"
            max-width="293"
            height="425"
            :elevation="hover ? 6 : 2"

          >
            <v-card-title class="d-flex flex-row-reverse">
              <v-btn rounded small text>Combos mixtos</v-btn>
            </v-card-title>
            <v-img
              :src="getImgUrl(product.images[0])"
              :lazy-src="getImgUrl(product.images[0])"
              min-width="293"
              max-width="293"
              class="white--text align-end"
              height="190px"
              @click="goToDetails(product)"
            >
            </v-img>

            <v-card-text class="text--primary" style="height: 120px">
              <div @click="goToDetails(product)">{{ product.title }}</div>
              <div>{{ product.description }}</div>

              <!-- <div>Precio: {{ item.price }}</div> -->
            </v-card-text>

            <v-divider style="margin: 11px 15px 0 15px !important;"></v-divider>

            <v-card-actions>
              <v-col>
                <div id="price">${{ product.price }}</div>
              </v-col>
              <v-col class="d-flex flex-row-reverse">
                <v-btn icon @click="addToCart(product)">
                  <v-badge
                    :content="amoutn"
                    :value="amoutn"
                    color="yellow darken-4"
                    overlap
                  >
                    <v-icon :color="amoutn > 0? 'rgba(255, 102, 0, 1)' : 'grey darken-1'">mdi-cart-variant</v-icon>
                  </v-badge>
                </v-btn>
                <v-btn icon @click.prevent="checkOnFavorites(product)">
                  <v-icon :color="favorite ? 'red' : 'grey darken-1'">{{ favorite ? 'mdi-heart' : 'mdi-heart-outline' }}</v-icon>
                </v-btn>
              </v-col>
            </v-card-actions>
          </v-card>
        </template>
      </v-hover>
    </div>

    <div id="horizontal-card" v-if="horizontal">
        <v-card
            class="ma-7 rounded-xl"
            elevation="1"
            min-height="260"
            max-height="260"
        >
        <v-row class="ma-0" style="height: 260px !important; min-height: 260px !important; max-height: 260px !important;">
          <v-col cols="4" class="pa-0">
            <v-img class="rounded-xl" height="260" max-height="260" :src="getImgUrl(product.images[0])" :lazy-src="getImgUrl(product.images[0])"></v-img>
          </v-col>
          <v-col class="ml-6">
            <v-row justify="space-between">
              <v-col cols="9">
                <p class="headline">{{product.title}}</p>
                <p>{{product.price}}</p>
              </v-col>
              <v-col cols="2">
                <v-btn icon @click.prevent="checkOnFavorites(product)">
                  <v-icon :color="favorite ? 'red' : 'grey darken-1'">{{ favorite ? 'mdi-heart' : 'mdi-heart-outline' }}</v-icon>
                </v-btn>
              </v-col>
            </v-row>
            <v-row class="ma-0 pr-4" style="height: 57%;" align="end" justify="space-between">
              <!-- <v-col  class="d-flex align-end"> -->
                <ValidationObserver ref="observer">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="adults"
                    rules="moreThanZero"
                  >
                    <label for="adults">Cantidad</label>
                    <v-text-field
                      id="adults"
                      v-model="product.amount"
                      type="number"
                      color="primary"
                      outlined
                      dense
                      style="width: 90px !important;"
                      :error-messages="errors"
                    ></v-text-field>
                  </ValidationProvider>
                </ValidationObserver>
              <!-- </v-col>
              <v-col align="right" class="d-flex flex-row-reverse align-end"> -->
                <v-btn rounded outlined elevation="0" @click="addToCart(product)" style="margin-bottom: 27px;" :color="amoutn > 0 ? 'rgba(255, 102, 0, 1)' : ''">
                  Agregar
                  <v-badge
                    :content="amoutn"
                    :value="amoutn"
                    color="yellow darken-4"
                    overlap
                  >
                    <v-icon :color="amoutn > 0 ? 'rgba(255, 102, 0, 1)' : ''">mdi-cart</v-icon>
                  </v-badge>
                </v-btn>
              <!-- </v-col> -->
            </v-row>
          </v-col>
        </v-row>
      </v-card>
    </div>
  </div>
</template>

<script>
import router from '../router'
import store from '../store'
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'

extend('moreThanZero', {
  validate (value) {
    return value >= 1
  },
  message: 'Escoja un valor vlido.'
})

export default {
  name: 'Product',
  props: {
    product: {
      type: Object,
      required: true
    },
    horizontal: {
      type: Boolean,
      default: false
    }
  },
  components: {
    ValidationObserver,
    ValidationProvider
  },
  computed: {

    amoutn: {
      get () {
        return store.getters.checkProductOnCart(this.product.id)
      }
    },

    favorite () {
      return this.isFavorite()
    }

  },
  methods: {

    addToCart (product) {
      this.$store.dispatch('addToCart', product)
    },

    checkOnFavorites () {
      if (store.getters.checkProductOnFavorites(this.product.id) === 0 && !this.product.isFavorite) {
        this.$store.dispatch('addToFavoriteProducts', this.product)
        this.product.isFavorite = true
      } else {
        this.$store.dispatch('removeFromFavoriteProducts', this.product)
        this.product.isFavorite = false
      }
    },

    isFavorite () {
      this.product.isFavorite = store.getters.checkProductOnFavorites(this.product.id) > 0
      return this.product.isFavorite
    },

    getImgUrl (image) {
      var images = require.context('../assets/', false)
      return images('./' + image)
    },

    goToDetails (product) {
      router.push({ path: '/restaurant/' + this.product.section.toLowerCase() + '/' + this.product.id + '/details' })
      // router.push({ name: 'product-details', params: { product: product } })
    }

  }
}
</script>

<style lang="scss" scoped>
  .v-card {
    // filter: drop-shadow(2px 1px 2px rgba(0, 0, 0, 0.161)) !important;
    // box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.2), 0px 0px 0px 0px rgba(0, 0, 0, 0.14), 100px 23px 100px 0px rgba(0, 0, 0, 0.12) !important;
    :hover{
      cursor: pointer;
    }

    .v-card__title button.v-btn {
      white-space: nowrap;
      text-align: center;
      font-family: Raleway;
      font-style: normal;
      font-weight: bold;
      font-size: 9px;
      color: rgba(255, 102, 0, 1);
      height: 21px;
      max-width: 100%;
      background-color: transparent;
      // box-shadow: 0px 2px 124px 9px rgba(0, 0, 0, 0.2),
      //   0px 1px 1px 0px rgba(0, 0, 0, 0.14), 1px 2px 66px 0px rgba(0, 0, 0, 0.12) !important;
      // box-shadow: 0px 2px 55px 9px rgba(0, 0, 0, 0.1), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 1px 2px 0px 0px rgba(0, 0, 0, -0.88) !important;
      box-shadow: 0px -452px 128px 0px rgba(0, 0, 0, 0.1), 0px 1px 35px 0px rgba(0, 0, 0, 0.1), 0px 0px 1px 0px rgba(0, 0, 0, 0) !important;
    }
    .v-image {
      /* padding: 10px 25px !important; */
      // background-image: url(http://localhost:8080/img/combo_mixto.7a21789b.png);
      // background-position: center;
      // background-repeat: no-repeat;
      /* background-size: contain !important; */
    }
    .v-card__text div:first-child {
      // white-space: nowrap;
      text-align: left;
      font-family: Raleway;
      font-style: normal;
      font-weight: bold;
      font-size: 16px;
      color: rgba(112, 112, 112, 1);
      margin-bottom: 0.5rem;
    }
    .v-card__text div:nth-child(2) {
      line-height: normal;
      text-align: justify;
      font-family: Raleway;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      color: rgba(112, 112, 112, 1);
    }
    .v-slide-group.v-item-group > .v-slide-group__next {
      position: absolute;
      height: 100% !important;
      min-height: 100% !important;
      right: 0;
      -webkit-box-shadow: 24px 0px 133px -72px rgb(255 255 255) !important;
      -moz-box-shadow: 20px 0px 120px 120px rgb(255 255 255) !important;
      box-shadow: 69px 0px 120px 120px rgb(255 255 255) !important;
      background: rgb(255, 255, 255);
      background: linear-gradient(
        90deg,
        rgba(255, 255, 255, 0.482533) -133%,
        rgba(255, 255, 255, 1) 80%,
        rgba(255, 255, 255, 1) 126%
      ) !important;
    }
  }
  #price {
    white-space: nowrap;
    text-align: left;
    font-family: Raleway;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    color: rgba(48, 153, 153, 1);
  }
  .col {
    padding: 0 12px;
  }

#horizontal-card{
  .v-card{
    font-family: 'Raleway-bold';
    color: rgba(112,112,112,1) !important;

    p{
      margin-bottom: 0 !important;
    }

    .headline{
      font-family: 'Raleway-bold' !important;
      color: rgba(112,112,112,1) !important;
    }

    #airline{
      font-family: 'Raleway';
      color: rgba(112,112,112,1) !important;
    }

    #img-col{
      background: rgba(217,239,239,1);
      height: 260;
      width: inherit;
      border-top-left-radius: inherit;
      border-bottom-left-radius: inherit;
      border-top-right-radius: unset;

      .v-image{
        margin: 2.5rem 0 !important;
      }
    }

    .v-divider{
      margin: 9px 0;
    }

    .row{
      margin: 0;
    }
  }
}
</style>
