<template>
  <v-container>
    <ValidationObserver ref="observer">
      <form autocomplete="false" @submit.prevent="updateProfile">
        <h2 style="margin: 25px 0;">Informacion personal</h2>
        <v-container style="padding: 12px 4rem;">

            <v-row justify="space-between">
              <ValidationProvider
                    v-slot="{ errors }"
                    name="name"
                    rules="required"
                  >
                <v-text-field
                  id="name"
                  v-model="profile.name"
                  label="Nombre"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
              <ValidationProvider
                    v-slot="{ errors }"
                    name="lastname"
                    rules="required"
                  >
                <v-text-field
                  id="lastname"
                  v-model="profile.lastname"
                  label="Apellidos"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
            </v-row>

            <v-row justify="space-between">
              <ValidationProvider
                    v-slot="{ errors }"
                    name="email"
                    rules="required"
                  >
                <v-text-field
                  id="email"
                  v-model="profile.email"
                  label="Correo electrnio"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
              <ValidationProvider
                    v-slot="{ errors }"
                    name="cell_phone"
                  >
                <v-text-field
                  id="cell_phone"
                  v-model="profile.cell_phone"
                  label="Telfono mvil"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
            </v-row>

            <v-row justify="space-between">
              <ValidationProvider
                    v-slot="{ errors }"
                    name="address1"
                    rules="required"
                  >
                <v-text-field
                  id="address1"
                  v-model="profile.address1"
                  label="Direccin (Linea 1)"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
              <ValidationProvider
                    v-slot="{ errors }"
                    name="address2"
                  >
                <v-text-field
                  id="address2"
                  v-model="profile.address2"
                  label="Direccin (Linea 2)"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
            </v-row>

            <v-row justify="space-between">
              <ValidationProvider
                    v-slot="{ errors }"
                    name="city"
                    rules="required"
                  >
                <v-text-field
                  id="city"
                  v-model="profile.city"
                  label="Cuidad"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
              <ValidationProvider
                    v-slot="{ errors }"
                    name="state"
                    rules="required"
                  >
                <v-text-field
                  id="state"
                  v-model="profile.state"
                  label="Estado"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
            </v-row>

            <v-row justify="space-between">
                <ValidationProvider
                  v-slot="{ errors }"
                  name="postal_code"
                >
                <v-text-field
                  id="postal_code"
                  v-model="profile.postal_code"
                  label="Cdigo postal"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
              <ValidationProvider
                  v-slot="{ errors }"
                  name="phone"
                >
                <v-text-field
                  id="phone"
                  v-model="profile.phone"
                  label="Telfono fijo"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
            </v-row>

            <v-row justify="space-between">
              <v-menu
                    ref="date_menu"
                    v-model="date_menu"
                    :close-on-content-click="false"
                    transition="scale-transition"
                    offset-y
                    max-width="290px"
                    min-width="auto"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <ValidationProvider
                        v-slot="{ errors }"
                        name="birth_date"
                        rules="required"
                      >
                        <v-text-field
                          id="birth_date"
                          v-model="profile.birth_date"
                          label="Fecha y hora del envo"
                          color="primary"
                          persistent-hint
                          append-inner-icon="mdi-calendar"
                          v-bind="attrs"
                          @blur="date = parseDate(profile.birth_date)"
                          v-on="on"
                          class="mx-auto"
                          style="width: 400px !important"
                          :error-messages="errors"
                          :hide-details="errors ? false : true"
                          clearable
                          dense
                          outlined
                          rounded
                        ></v-text-field>
                      </ValidationProvider>
                    </template>
                    <v-date-picker
                      v-model="date"
                      no-title
                      color="primary"
                      @input="date_menu = false"
                    ></v-date-picker>
              </v-menu>
            </v-row>

            <h3 style="font-weight: normal;">GNERO</h3>
            <v-row style="margin-bottom: 2rem !important">
              <v-checkbox class="mx-2 mt-0" v-model="profile.gender" label="Femenino" value="F"></v-checkbox>
              <v-checkbox class="mx-2 mt-0" v-model="profile.gender" label="Masculino" value="M"></v-checkbox>
            </v-row>

            <h3>Cambiar contrasea</h3>
            <v-row justify="space-between">
              <ValidationProvider
                    v-slot="{ errors }"
                    name="password"
                    rules="required"
                  >
                <v-text-field
                  id="password"
                  v-model="profile.password"
                  type="password"
                  label="Contrasea"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
              <ValidationProvider
                    v-slot="{ errors }"
                    name="password_confirm"
                    :rules="profile.password != null && profile.password != '' ? 'required' : ''"
                  >
                <v-text-field
                  id="password_confirm"
                  v-model="profile.password_confirm"
                  type="password"
                  label="Confirmar contrasea"
                  rounded
                  outlined
                  dense
                  class="mx-auto"
                  style="width: 400px !important"
                  :error-messages="errors"
                  :hide-details="errors ? false : true"
                ></v-text-field>
              </ValidationProvider>
            </v-row>

            <v-row justify="space-between">
              <v-checkbox
                v-model="profile.accept_notifications"
                label="Acepto recibir noticiones y promociones de CUBAMAX"
              ></v-checkbox>
            </v-row>

            <v-row style="margin: 5rem 0 3rem !important" justify="center">
              <v-btn type="submit" class="mx-2" rounded elevation="0" color="primary">Actualizar</v-btn>
            </v-row>
        </v-container>
      </form>
    </ValidationObserver>
  </v-container>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'

extend('required', {
  validate (value) {
    return {
      required: true,
      valid: ['', null, undefined].indexOf(value) === -1
    }
  },
  message: 'Rellene este campo.',
  computesRequired: true
})

extend('afterToday', {
  validate (value) {
    const [day, month, year] = value.split('/')
    const date = new Date(`${month}/${day}/${year}`)
    const today = new Date()
    return date.getTime() >= today.getTime()
  },
  message: 'Escoja una fecha posterior a la actual.'
})

extend('moreThanZero', {
  validate (value) {
    return value >= 1
  },
  message: 'Escoja un valor vlido.'
})

extend('positive', {
  validate (value) {
    return value >= 0
  },
  message: 'Escoja un valor vlido.'
})

export default {
  name: 'Profile',
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data: function () {
    return {
      date: null,
      date_menu: null,
      profile: {
        // name: null,
        // lastname: null,
        // phone: null,
        // id: null,
        // address1: null,
        // address2: null,
        // city: null,
        // state: null,
        // alternative_destinatary: null,
        // birth_date: null,
        // deny_alternative_destinatary: false
        name: 'Alfredo Suarez',
        lastname: 'Garrido',
        email: 'cubamax@gmail.com',
        cell_phone: '+1 568 5546 568',
        id: '89565123165',
        address1: 'Cuba',
        address2: null,
        city: 'La habana',
        state: 'Playa',
        postal_code: '',
        phone: null,
        birth_date: null,
        gender: 'M',
        password: null,
        password_confirm: null,
        accept_notifications: false
      }
    }
  },
  watch: {
    date (val) {
      this.profile.birth_date = this.formatDate(this.date)
    }
  },
  methods: {
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    updateProfile () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          // const alternative = !this.profile.deny_alternative_destinatary ? '   (Destinatario alternativo: ' + this.profile.alternative_destinatary + ')' : ''
          // const newDestinatary = this.profile.name + ' ' + this.profile.lastname + alternative
          // console.log('alternative', alternative)
          // console.log('newDestinatary', newDestinatary)
          // this.$emit('newDestinatarySaved', newDestinatary)
          alert('Usuario actualizado!')
        }
      })
    },
    cancel () {
      this.$emit('cancel')
    }
  }
}
</script>

<style lang="scss" scoped>
.row{
  margin: 7px 0 !important;
}
h3{
  text-transform: uppercase;
}
.v-input--checkbox label{
  font-weight: normal;
}
</style>
