<template>
  <v-main>
    <v-container>
      <v-row align="center">
        <h1 class="my-14">CARRITO DE COMPRAS</h1>
        <v-spacer></v-spacer>
        <v-btn type="submit" class="mx-2" rounded elevation="0" color="error" @click="clearCart()">
          Vaciar carrito
          <v-icon class="ml-2">mdi-delete</v-icon>
        </v-btn>
      </v-row>
      <v-row justify="center" class="my-10">
        <v-btn type="button" class="mx-2" rounded elevation="0" color="amber" to="/home">Seguir comprando</v-btn>
        <v-btn type="submit" class="mx-2" rounded elevation="0" color="primary" to="/order/confirm">Comprar</v-btn>
      </v-row>
      <v-row v-for="(product, i) in products" :key="i" style="border-bottom: 1px solid lightgray;">
        <v-col>
          <v-row></v-row>
          <v-row>
            <v-col>
              <v-row class="pb-0">
                <v-col cols="6">
                  <v-img class="rounded-lg" height="192" max-height="192" width="210" max-width="210px" :src="getImgUrl(product.images[0])" :lazy-src="getImgUrl(product.images[0])"></v-img>
                </v-col>
                <v-col cols="6" class="d-flex flex-column justify-space-between">

                  <v-row>
                    <v-col class="py-0">
                      <p>Nombre del productos</p>
                      <p>Categoria</p>
                    </v-col>
                  </v-row>

                  <v-row align="end">
                    <v-col class="py-0 pl-0">
                      <v-btn text class="mx-2 px-0" color="amber darken-2" @click="removeFromCart(product)">
                        Eliminar del carrito
                      </v-btn>
                      <v-btn text class="mx-2 px-0" color="amber darken-2" @click="checkOnFavorites(product)">
                        Mover a la lista de deseos
                      </v-btn>
                    </v-col>
                  </v-row>

                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-col>

        <v-col cols="2">
          <v-row justify="center"><p>Precio x unidad</p></v-row>
          <v-row justify="center"><p>$25</p></v-row>
        </v-col>

        <v-col cols="2">
          <v-row justify="center">Cantidad</v-row>
          <v-row justify="center">3</v-row>
        </v-col>

        <v-col cols="2">
          <v-row justify="center">Subtotal</v-row>
          <v-row justify="center">$75</v-row>
        </v-col>

      </v-row>
    </v-container>
  </v-main>
</template>

<script>
// import { mapState } from 'vuex'

export default {
  name: 'Cart',
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      products: this.$store.getters.cart(),
      pageProducts: []
    }
  },
  // computed: mapState(['cart']),
  // watch: {
  //   cart (newValue, oldValue) {
  //     console.log('old', oldValue)
  //     console.log('new', newValue)
  //   }
  // },
  methods: {
    getImgUrl (image) {
      var images = require.context('../../assets', false)
      return images('./' + image)
    },
    clearCart () {
      this.$store.dispatch('clearCart')
      this.products = this.$store.getters.cart()
    },
    checkOnFavorites (product) {
      if (this.$store.getters.checkProductOnFavorites(product.id) === 0) {
        this.$store.dispatch('addToFavoriteProducts', product)
        product.isFavorite = true
      } else {
        this.$store.dispatch('removeFromFavoriteProducts', product)
        product.isFavorite = false
      }
      this.removeFromCart(product)
    },
    removeFromCart (product) {
      this.$store.dispatch('removeFromCart', product)
      this.products = this.$store.getters.cart()
    }
  }
}
</script>

<style lang="scss" scoped>
.row.justify-center{
  font-weight: bold;
}
p {
  margin-bottom: 0;
}
.v-image{
  box-shadow: 0px 0px 9px -1px rgb(0 0 0 / 20%), 0px 0px 0px 0px rgb(0 0 0 / 14%), 0px 1px 0px 0px rgb(0 0 0 / 12%) !important;
}
.row{
  margin: 0;
}
</style>
