import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/home'
import Restaurant from '@/components/Restaurant.vue'
import AcceptOrder from '../views/order/accept-order.vue'
import PayOrder from '../views/order/pay-order.vue'
import Settings from '../views/settings'
import Profile from '../views/settings/Profile.vue'
import Cards from '../views/settings/Cards.vue'
import OrderHistory from '../views/settings/OrderHistory.vue'
import Destinataries from '../views/settings/Destinataries.vue'
import WishList from '../views/wishes'
import Cart from '../views/cart'

Vue.use(VueRouter)

const routes = [
  {
    path: '/', redirect: 'home'
  },
  {
    path: '/home', name: 'home', component: Home
  },
  {
    path: '/restaurant/Cubamax/:id/details', name: 'restaurant', component: Restaurant
  },
  {
    path: '/restaurant/:section/:id/details', name: 'product-details', component: Restaurant
  },
  {
    path: '/order/confirm', name: 'order-confirm', component: AcceptOrder
  },
  {
    path: '/order/pay', name: 'order-pay', component: PayOrder
  },
  {
    path: '/settings',
    component: Settings,
    children: [
      { path: '', component: Profile },
      { path: 'profile', name: 'profile', component: Profile },
      { path: 'cards', name: 'cards', component: Cards },
      { path: 'order-history', name: 'order-history', component: OrderHistory },
      { path: 'contacts', name: 'destinataries', component: Destinataries }
    ]
  },
  {
    path: '/wish-list', name: 'wish-list', component: WishList
  },
  {
    path: '/cart', name: 'cart', component: Cart
  }

]

const scrollBehavior = (to, from, savedPosition) => {
  if (savedPosition) {
    return savedPosition
  }
  if (to.hash) {
    return { selector: to.hash }
  }
  return { x: 0, y: 0 }
}
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior
})

export default router
