<template>
  <v-main>
    <v-container>
      <h1 class="my-14">COLOCAR LA ORDEN</h1>
      <v-simple-table
        fixed-header
        height="300px"
        class="mb-5"
      >
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">Producto</th>
              <th class="text-left">Cantidad</th>
              <th class="text-left">Precio</th>
              <th class="text-left">Importe</th>
              <th class="text-left"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, i) in products" :key="i">
              <td>
                <p>{{ item.title }}</p>
              </td>
              <td>{{ item.count }}</td>
              <td>{{ item.price }}</td>
              <td>{{ item.price + 1.60 }}</td>
              <td>
                <v-btn icon @click="removeFromCart(item)">
                  <v-icon>mdi-delete</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
      <div  v-if="products.length > 0">
        <v-row align="center" justify="end">
          <span>COSTO DE ENVO</span><span class="mx-3">1.60</span>
        </v-row>
        <v-row align="center" justify="end">
          <span>Fue aplicado a la compra de productos</span><span class="mx-3">1.60</span>
        </v-row>
        <v-row align="center" justify="end">
          <v-col cols="auto">
            <p>Precio final</p>
            <p class="text-center">1.60</p>
          </v-col>
          <v-col cols="auto">
            <p>Importe final</p>
            <p class="text-center">1.60</p>
          </v-col>
        </v-row>
      </div>

      <div v-if="products.length > 0">
        <v-row>
          <v-col>
            <label for="field1">Seleccionar destinatario</label>
            <v-autocomplete
              id="field1"
              v-model="client"
              :items="clients"
              color="primary"
              dense
              clearable
              outlined
              chips
              multiple
            >
            </v-autocomplete>
          </v-col>
      </v-row>
      <v-row align="center" justify="end">
        <v-btn rounded color="primary" @click="show = !show">
          Agregar nuevo
        </v-btn>
      </v-row>
      </div>

      <NewClient v-if="show" @newDestinatarySaved="addNewDestinatary($event)" @cancel="show = false" />

      <v-row class="my-15" justify="center" v-if="!show">
        <v-btn class="mx-2" rounded elevation="0" color="primary" :disabled="client == null || client == '' || client.length == 0" @click="makeRequest">Comprar</v-btn>
      </v-row>

    </v-container>
  </v-main>
</template>

<script>
import NewClient from './new-client'
import router from '../../router'

export default {
  name: 'AcceptOrder',
  components: {
    NewClient
  },
  data () {
    return {
      client: null,
      show: false,
      clients: [
        'Ramon Hernandez   (Destinatario alternativo: Gabril Cruz)',
        'Juan Cabrales',
        'Maria La Pinta',
        'Pedro Pablo   (Destinatario alternativo: Juana Lopez)'
      ],
      products: []
    }
  },
  created () {
    var cart = this.$store.getters.cart()
    cart.map((p) => {
      p.count = this.$store.getters.checkProductOnCart(p.id) || 0
      return p
    })
    cart = [...new Set(cart)]
    this.products = cart
  },
  methods: {
    addNewDestinatary (event) {
      console.log(event)
      this.show = false
      this.clients.push(event)
      this.client.push(event)
    },
    makeRequest () {
      router.push({ name: 'order-pay' })
    },
    removeFromCart (product) {
      this.$store.dispatch('removeFromCart', product)
      this.products = this.$store.getters.cart()
    }
  }
}
</script>

<style lang="scss" scoped>
.row{
  margin: 0;
}
p{
  margin-bottom: 0;
}
</style>
