<template>
  <v-main>
    <v-container class="py-14">
      <h1>Lista de deseos</h1>
      <v-row>
        <v-card
            v-for="(product, i) in products"
            :key="i"
            class="my-7 mx-11 rounded-xl"
            elevation="1"
            min-height="192"
            max-height="192"
            width="477"
            max-width="477"
        >
        <v-row class="ma-0" style="height: 192px !important; min-height: 192px !important; max-height: 192px !important;">
          <v-col class="pa-0">
            <v-img class="rounded-xl" height="192" max-height="192" width="210" max-width="210px" :src="getImgUrl(product.images[0])" :lazy-src="getImgUrl(product.images[0])"></v-img>
          </v-col>
          <v-col class="pl-0 d-flex flex-column justify-space-around">
            <v-row justify="space-between">
              <v-col>
                <p>Nombre del restaurante</p>
                <p>Tipo de concina</p>
                <p>Tipo de entrega</p>
              </v-col>
            </v-row>
            <v-row align="center" justify="space-between">
              <v-col align="center">
                <div style="border: 1px solid lightgrey !important; border-radius: 32px; padding: 0px 9px;">
                  <v-btn icon class="mx-2" @click="addToCart(product)">
                    <v-icon>mdi-cart</v-icon>
                  </v-btn>
                  <v-btn icon class="mx-2" @click="removeFromFavoriteProducts(product)">
                    <v-icon>mdi-delete</v-icon>
                  </v-btn>
                  <v-btn icon class="mx-2">
                    <v-icon>mdi-share-variant</v-icon>
                  </v-btn>
                </div>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
        </v-card>
      </v-row>
    </v-container>
  </v-main>
</template>

<script>
import router from '../../router'
// import { mapState } from 'vuex'

export default {
  name: 'WishList',
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      products: this.$store.getters.favoriteProducts(),
      pageProducts: []
    }
  },
  // computed: mapState(['favoriteProducts']),
  // watch: {
  //   favoriteProducts (newValue, oldValue) {
  //     console.log('old', oldValue)
  //     console.log('new', newValue)
  //     this.products = this.$store.getters.favoriteProducts()
  //   }
  // },
  methods: {
    addToCart (product) {
      this.$store.dispatch('addToCart', product)
      this.removeFromFavoriteProducts(product)
    },
    removeFromCart (product) {
      this.$store.dispatch('removeFromCart', product)
    },
    removeFromFavoriteProducts (product) {
      this.$store.dispatch('removeFromFavoriteProducts', product)
      this.products = this.$store.getters.favoriteProducts()
    },
    checkOnFavorites (product) {
      if (this.$store.getters.checkProductOnFavorites(this.product.id) === 0) {
        this.$store.dispatch('addToFavoriteProducts', product)
        product.isFavorite = true
      } else {
        this.$store.dispatch('removeFromFavoriteProducts', product)
        product.isFavorite = false
      }
    },
    getImgUrl (image) {
      var images = require.context('../../assets/', false)
      return images('./' + image)
    },
    goToDetails (product) {
      router.push({ path: '/restaurant/' + this.product.section.toLowerCase() + '/' + this.product.id + '/details' })
      // router.push({ name: 'product-details', params: { product: product } })
    }
  }
}
</script>

<style lang="scss" scoped>
p{
  margin-bottom: 0;
}
.row{
  margin: 0;
}
